import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProductOnShopComponent } from './product-on-shop.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { ModalLinkProductEcommerceModule } from 'src/app/sales/menu/manage/ecommerce/components/modal-link-product-ecommerce/modal-link-product-ecommerce.module';
const routes: Routes = [
  {
    path: '',
    component: ProductOnShopComponent
  },
]

@NgModule({
  declarations: [ProductOnShopComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ScrollingModule,
    ModalLinkProductEcommerceModule
  ]
})
export class ProductOnShopModule { }
